*,
*::after,
*::before {
  margin: 0px;
  padding: 0px;
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

html {
  font-size: 62.5%;
  /* 1 rem = 10px but bad practice to use px
  default browser font size is 16px
  useing would break user font-sizes (accessibility)
  100% is 16px
  to covert, we calculate 10/16 = 62.5%
 */
}

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*font-family: sans-serif;*/
  font-weight: 400;
  font-size: 14px;
  line-height: 1.7;
  color: #444;
  padding: 1rem;
}

.background {
  margin-top: -20px;
  height: calc(100vh - 72px);
  background-image: url("./images/mountain.jpg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
}



